<template>
	<div id="basesSetInfo">
		<div id="inbound" class="wzborder"><span class="mtitle"></span>
			<p class="infolongb"><b>socks </b><input name='sport' v-model='inbound.sport'></p>
			<p class="infolongb"><b>HTTP </b><input name='hport' v-model='inbound.hport'></p>
			<p class="center"><i>*</i></p>
		</div>
		<div id='idBsetdns' class='wzborder'><span class='mtitle'>DNS</span>
			<p><b>DNS</b><textarea name='dns' placeholder='8.8.8.8,1.1.1.1' v-model="dns"></textarea></p>
		</div>
		<div id="idlogset" class="wzborder"><span class="mtitle">log </span>
			<p class="infolongb"><b></b><input name="access" placeholder="" v-model="log.access"></p>
			<p class="infolongb"><b></b><input name="error" placeholder="" v-model="log.error"></p>
			<p class="infolongb"><b></b><select v-model="log.loglevel" name="loglevel">
					<option>debug</option>
					<option>info</option>
					<option>warning</option>
					<option>error</option>
					<option>none</option>
				</select></p>
			<p class="center"><i>* stdout<br>* none</i></p>
		</div>
		<div id="idsubset" class="wzborder"><span class="mtitle"></span>
			<p class="infolongb"><b></b><input name="maxSubsers" v-model="evset.maxSubsers"></p>
			<p><b></b><input type="checkbox" name="isRandsub" v-model="evset.isRandsub">
				<p class="tip">* </p>
		</div>
		<div class="wzborder csevset"><span class="mtitle"></span>
			<p><input name="isLocalStorage" type="checkbox" v-model="evset.isLocalStorage"><span> localStorage</span></p>
			<p><input name="runonStart" type="checkbox" v-model="evset.runonStart"><span> v2ray</span></p>
			<p><input name="isResize" type="checkbox" v-model="evset.isResize"><span></span></p>
			<p class="tip">*  localStorage  elecV2.json <br>* </p>
		</div>
		<p class="center"><button class="longButton" @click="saveBset"></button></p>
	</div>
</template>

<script>
export default {
	name: 'CompbasesSet',
	data(){
		return {
			inbound: this.$root.gConf.inbound,
			log: this.$root.gConf.log,
			evset: this.$root.gConf.evset,
			gdns: this.$root.gConf.dns
		}
	},
	computed: {
		dns: {
			get: function(){
				return this.gdns.join(",")
			},
			set: function(newValue){
				this.gdns = newValue.split(",")
			}
		}
	},
	methods: {
		saveBset: function(){
			if (this.inbound.sport == this.inbound.hport) {
				this.$root.alertElec("http  socks ")
				return false
			}
			if (this.inbound.sport && this.inbound.hport) {
				this.inbound.sport = Number(this.inbound.sport)
				this.inbound.hport = Number(this.inbound.hport)
			} else {
				this.$root.alertElec("http  socks ")
				return false
			}
			this.evset.maxSubsers = Number(this.evset.maxSubsers) || 10
			this.$root.gConf.dns = this.gdns
			this.$root.saveGconf()
			this.$root.alertElec("")
		}
	}
}
</script>