<template>
<div id="advanSetInfo">
	<div class="wzborder" id="idtbcs"><span class="mtitle"></span>
		<p><b>MUX</b><input type="checkbox" name="mux" v-model="outBset.mux"><b class="cslongb">concurrency</b><input name="concurrency" v-model="outBset.concurrency" placeholder="1-1024"></p>
		<p><b class="cslongb">tcpfastopen</b><input type="checkbox" name="tcpfastopen" v-model="outBset.tcpfastopen"><b>TLS</b><select name="tls" v-model="outBset.tls">
				<option>none</option>
				<option>tls</option>
			</select></p>
		<p><b class="cslongb">allowInsecure</b><input type="checkbox" name="allowInsecure" v-model="outBset.allowInsecure"></p>
		<p class="infolongb"><b>domainStrategy</b><select name="domainStrategy" v-model="outBset.domainStrategy">
				<option>AsIs</option>
				<option>IPIfNonMatch</option>
				<option>IPOnDemand</option>
			</select></p>
	</div>
	<div class="wzborder csoutbset"><span class="mtitle"></span>
		<p class="center"></p>
		<p class="csoutborder"><b></b><select v-model="outBset.outborder[0]">
				<option value="proxy"></option>
				<option value="direct"></option>
				<option value="block"></option>
			</select><select v-model="outBset.outborder[1]">
				<option value="proxy"></option>
				<option value="direct"></option>
				<option value="block"></option>
			</select></p>
		<p class="tip">* <br>*   -   - </p>
		<p class="csroutorder"><b></b><select v-model="outBset.routorder[0]">
				<option value="proxy"></option>
				<option value="direct"></option>
				<option value="block"></option>
			</select><select v-model="outBset.routorder[1]">
				<option value="proxy"></option>
				<option value="direct"></option>
				<option value="block"></option>
			</select></p>
	</div>
	<div class="wzborder cskcpset"><span class="mtitle">KCP</span>
		<p><b>mtu</b><input name='mtu' v-model='outBset.kcp[0]' placeholder=' 576 - 1460'></p>
		<p><b>tti</b><input name='tti' v-model='outBset.kcp[1]' placeholder=' 10 - 100'></p>
		<p class="infolongb"><b>ulinkCapacity</b><input name='uplinkCapacity' v-model='outBset.kcp[2]' placeholder=' MB/s'></p>
		<p class="infolongb"><b>dlinkCapacity</b><input name='downlinkCapacity' v-model='outBset.kcp[3]' placeholder=' MB/s'></p>
		<p><b class="cslongb">congestion</b><input type="checkbox" name="congestion" v-model="outBset.kcp[4]"><b></b><select v-model="outBset.kcp[7]" name="type">
				<option>none</option>
				<option>srtp</option>
				<option>utp</option>
				<option>wechat-video</option>
				<option>dtls</option>
				<option>wireguard</option>
			</select></p>
		<p><b>rBSize</b><input name='readBufferSize' v-model='outBset.kcp[5]' placeholder=' MB'><b>wBSize</b><input name='writeBufferSize' v-model='outBset.kcp[6]' placeholder=' MB'></p>
	</div>
	<p class="center"><button @click="saveadvanSet"></button></p>
</div>
</template>

<script>
export default {
	name: 'CompadvanSet',
	data(){
		return {
			outBset: this.$root.gConf.outBset
		}
	},
	methods: {
		saveadvanSet: function(){
			this.outBset.concurrency = Number(this.outBset.concurrency)
			if ((this.outBset.outborder[0] == this.outBset.outborder[1]) || (this.outBset.routorder[0] == this.outBset.routorder[1])) {
				this.$root.alertElec("")
				return
			}
			for (let v of ["direct", "block", "proxy"]) {
				if (this.outBset.outborder.indexOf(v) == -1) this.outBset.outborder[2] = v
				if (this.outBset.routorder.indexOf(v) == -1) this.outBset.routorder[2] = v
			}
			this.outBset.kcp[0] = Number(this.outBset.kcp[0]) || 1350
			this.outBset.kcp[1] = Number(this.outBset.kcp[1]) || 50
			this.outBset.kcp[2] = Number(this.outBset.kcp[2]) || 5
			this.outBset.kcp[3] = Number(this.outBset.kcp[3]) || 20
			this.outBset.kcp[5] = Number(this.outBset.kcp[5]) || 2
			this.outBset.kcp[6] = Number(this.outBset.kcp[6]) || 2

			this.$root.saveGconf()
			this.$root.alertElec("")
		}
	}
}
</script>